import React, { Component } from 'react';


export default class Header extends Component {
  constructor(props) {
      super(props);
      this.state = {
        mouseX: 0,
        mouseY: 0,
        bgX: 0,
        bgY: 0,
        dx: 0,
        dy: 0,
        windowWidth: window.innerWidth,
        windowHeight: window.innerHeight,
        offsetRotation: 0.0,
        offsetScale: 1.0,
        offsetX: 0.0,
        offsetY: 0.0,
        radius: (window.innerWidth/2)-8,
        radiusY: (window.innerHeight/2),
        slices: 12,
        zoom: .5
      };
    }

  _onMouseMove(e) {
    this.setState({
      mouseX: e.screenX,
      mouseY: e.screenY,
    });
  }

  ease() {
     this.setState(state => ({
       dx: this.state.mouseX - this.state.bgX,
       dy: this.state.mouseY - this.state.bgY,
       bgX: this.state.bgX + (this.state.dx / 100),
       bgY: this.state.bgY + (this.state.dy / 100),
     }));
     this.draw();
   }

   draw() {
    const canvas = this.refs.canvas
    const context = canvas.getContext("2d")
    const image = new Image();
    image.src = '../images/header-background.jpg';

    var cx, cy, i, index, ref, results, scale, step;
      // this.state.windowWidth = this.state.radius * 2;
      context.fillStyle = context.createPattern(image, 'repeat');
      scale = this.state.zoom * (this.state.radius / Math.min(this.state.windowWidth, this.state.windowHeight));
      step = (Math.PI * 2) / 20;
      cx = this.state.windowWidth / 2;
      cy = this.state.windowHeight / 2;
      results = [];
      for (index = i = 0, ref = 20; (0 <= ref ? i <= ref : i >= ref); index = 0 <= ref ? ++i : --i) {
        context.save();
        context.translate(this.state.radius, this.state.radiusY);
        context.rotate(index * step);
        context.beginPath();
        context.moveTo(-0.5, -0.5);
        context.arc(0, 0, (2 * window.innerWidth), step * -0.51, step * 0.51);
        context.lineTo(0.5, 0.5);
        context.closePath();
        context.rotate(Math.PI / 2);
        context.scale(scale, scale);
        context.scale([-1, 1][index % 2], 1);
        context.translate(this.state.bgX - cx, this.state.bgY);
        context.rotate(this.offsetRotation);
        context.scale(this.offsetScale, this.offsetScale);
        context.fill();
        results.push(context.restore());
      }
  }

  componentDidMount() {
    this.interval = setInterval(() => this.ease(), 1)
  }

  render() {
    let resumeData = this.props.resumeData;
    const { mouseX, mouseY, bgX, bgY } = this.state;
    const style = {

    }
    const canvas = {
      // background: `url('../images/header-background.jpg') ${this.state.bgX}px ${this.state.bgY}px`,
      // background: `red`,
      position: `absolute`,
      zIndex: `-1`,
      left: `0`,
      top: `0`,
    }


    return (
      <React.Fragment>


      {/*generated code*/}
      <header id="home" onMouseMove={this._onMouseMove.bind(this)} style={style}>
        <canvas ref="canvas" width={this.state.windowWidth} height ={this.state.windowHeight} style={canvas}/>

         <nav id="nav-wrap">
            <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
          <a className="mobile-btn" href="#" title="Hide navigation">Hide navigation</a>
            <ul id="nav" className="nav">
               <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
               <li><a className="smoothscroll" href="#about">About</a></li>
               <li><a className="smoothscroll" href="#portfolio">Portfolio</a></li>
               <li><a className="smoothscroll" href="#testimonials">Testimonials</a></li>
               <li><a className="smoothscroll" href="#contact">Contact</a></li>
            </ul>
         </nav>

         <div className="row banner">
            <div className="banner-text">
              <h1 className="responsive-headline">{resumeData.name}</h1>
              <h3>{resumeData.role}
              </h3>

               <ul className="social">
                  {
                    resumeData.socialLinks && resumeData.socialLinks.map(item =>{
                      return(
                              <li key={item.name}>
                                <a href={item.url} target="_blank"><i className={item.className}></i></a>
                              </li>
                            )
                          }
                    )
                  }
               </ul>
            </div>
         </div>

         <p className="scrolldown">
            <a className="smoothscroll" href="#about"><i className="icon-down-circle"></i></a>
         </p>

      </header>
      </React.Fragment>
    );
  }
}
